// 登入、購物車、logo、搜尋之Navbar
<template>
  <div class="w-100 p-0">
      <div class="bg-black">
        <div class="container">
          <div class="top-navbar d-flex justify-content-between">
            <button class="smallpad-btn">
              <i class="bi bi-list"></i>
              <div>
                Shop
              </div>
            </button>
            <div class="d-flex ms-2">
              <div class="text-white h6 m-0 top-navbar-order">Order online or call</div>
              <a href="#" class="text-white ms-2 text-decoration-none fw-bold top-navbar-phone">800-558-6887</a>
            </div>
            <div id="navbarText ms-auto">
              <ul class="d-flex align-items-center h-100 me-3">
                <li class="nav-item">
                  <router-link to="/user/login" class="nav-link top-navbar-icon py-0 m-0">
                      <div class="d-flex align-items-center">
                        <i class="bi bi-person-circle m-0 me-2 text-white h4"></i>
                        <div class="user-navbar-text-color">Login</div>
                      </div>
                  </router-link>
                </li>
                <li class="nav-item smallpad-cart">
                  <router-link to="/user/cart" class="nav-link top-navbar-icon py-0 m-0">
                    <i class="bi bi-cart4 position-relative m-0 me-2 h4 text-white ">
                      <span v-if="cart_count>0" class="position-absolute top-50 start-100 translate-middle bg-danger rounded-circle cart-badge">
                        {{cart_count}}
                      </span>
                    </i>
                  </router-link>
                </li>
                <div class="dropdown">
                  <div class="dropdown-toggle top-navbar-icon py-0 m-0" id="dropdownMenuButton2" data-bs-toggle="dropdown"  data-bs-auto-close="outside" aria-expanded="false" @click="goToBottom">
                    <div class="d-flex align-items-center">
                      <i class="bi bi-cart4 position-relative m-0 me-2 h4 text-white ">
                      <span v-if="cart_count>0" class="position-absolute top-50 start-100 translate-middle bg-danger rounded-circle cart-badge">
                        {{cart_count}}
                      </span>
                      </i>
                    <div class="user-navbar-text-color">Your Cart</div>
                    </div>
                  </div>
                  <div class="dropdown-menu ptn-cart-summary">
                    <div class="cart-content">
                      <table class="table">
                        <tbody>
                          <tr v-if="carts.length==0">
                            <td>Nothing in cart</td>
                          </tr>
                          <tr v-for="cart in carts" :key="cart.id">
                            <td class="price">{{cart.qty}}</td>
                            <td class="title">{{cart.product.title}}</td>
                            <td class="text-end">${{cart.total}}</td>
                          </tr>
                          <tr v-if="carts.length!==0" class="border-0">
                            <td class="total">
                              Total
                            </td>
                            <td colspan="2" class="text-end total">
                              ${{Math.round(total*100)/100}}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <router-link to="/user/cart" class="btn btn-cart-green cart-proceed-button fw-bold w-100" @click="closeDropdownList">Edit Cart</router-link>
                    <div class="text-center" @click="closeDropdownList">Or continue shopping</div>
                  </div>
                </div>
                <li v-if="is_logout" class="nav-item ms-auto">
                  <!-- 點擊觸發logOut method -->
                  <a class="nav-link" href="#" @click.prevent="logOut">登出</a>
                </li>
              </ul>
            </div>
          </div>
      </div>
      </div>
      <div class="smallpad-btn-area" id="navbarText">
        <ul class="mb-0 justify-content-around w-100 smallpad-btn-area-ul">
          <li class="p-0">
            <router-link to="#" class="catogory-navbar-text m-0 fw-bold" @click.prevent="getCatrgory">Nuts & Seeds</router-link>
          </li>
          <li class="nav-item">
            <router-link to="#" class="catogory-navbar-text m-0 fw-bold" @click.prevent="getCatrgory">Dried Fruit</router-link>
          </li>
          <li class="nav-item p-0">
            <router-link to="#" class="catogory-navbar-text m-0 fw-bold" @click.prevent="getCatrgory">Chocolates & Sweets</router-link>
          </li>
        </ul>
      </div>
    <div class="coupon-info-tablet">Enter Coupon : nice, get 20% discount !!</div>
    <div class="top-banner">
      <div class="container">
          <!-- <div class="d-flex justify-content-between"> -->
          <div class="top-banner-area">
            <div class="top-banner-img">
              <router-link to="/user/index">
                <img src="https://nuts.com/nutshell/img/nuts-logo-420b6a54.svg" alt="">
              </router-link>
            </div>
            <div class="top-banner-search d-flex flex-column justify-content-center align-items-center">
              <div class="w-75">
                <div class="search">
                  <input type="text" class="form-control top-banner-input" placeholder="Search for a product" v-model="search">
                    <ul class="search-ul" @click="clickToClose">
                      <li v-for="item in searchArray" :key="item.id">
                          <!-- <router-link :to="`/user/usersingleproduct/${item.id}`" class="dropdown-item"> -->
                          <a class="dropdown-item" @click.prevent="pushToSingle(item.id)">
                            <div class="d-flex search-card">
                              <img :src="item.imageUrl" alt="" class="search-img">
                              <div class="d-flex flex-column justify-content-center">
                                <div>{{item.title}}</div>
                                <p>${{item.price}}/{{item.unit}}</p>
                              </div>
                            </div>
                          <!-- </router-link> -->
                          </a>
                      </li>
                    </ul>
                  <button type="button" class="btn btn-warning search-button">Search</button>
                  <div class="peanut-icon"></div>
                </div>
                <div class="search-content">
                  <p class="pt-2">
                    <strong>Looking for </strong>
                    <a href="#" class="search-text">Almonds</a> ,
                    <a href="#" class="search-text">Cashews</a> ,
                    <a href="#" class="search-text">Walnuts</a>
                    <strong> or </strong>
                    <a href="#" class="search-text">Organic</a> ?
                  </p>
                </div>
              </div>
            </div>
            <div class="col-2 coupon-info d-flex flex-column align-items-center justify-content-center">
              <div class="coupon-icon-img"></div>
              <div class="mt-2 fw-bold">Enter Coupon : nice</div>
              <div class="text-center">Get 20% discount !!</div>
            </div>
          </div>
      </div>
    </div>

      <!-- smallpad時出現的search input -->
      <div class="smallpad-search">
        <div class="input-group">
          <input type="text" class="form-control top-banner-input" placeholder="Search for a product" v-model="search">
          <ul class="search-ul" @click="clickToClose">
            <li v-for="item in searchArray" :key="item.id">
                <a class="dropdown-item" @click.prevent="pushToSingle(item.id)">
                  <div class="d-flex search-card">
                    <img :src="item.imageUrl" alt="" class="search-img">
                    <div class="d-flex flex-column justify-content-center">
                      <div>{{item.title}}</div>
                      <p>${{item.price}}/{{item.unit}}</p>
                    </div>
                  </div>
                </a>
            </li>
          </ul>
          <button type="button" class="btn btn-warning search-button">Search</button>
        </div>
      </div>

  </div>
</template>

<script>
// vue cli 加入這行使用$ Lint才不會抱錯
/*eslint-disable*/
import Dropdown from 'bootstrap/js/dist/dropdown'
export default {
  data  () {
    return {
      is_logout: false,
      cart_count: 0, //  接收購物車數量
      search: '', // 綁定search input
      products: [], // 存取api回傳的全部產品
      dropdownList: {}, // 宣告下拉選單實體
      carts: {}, // 存取api回傳的全部購物車內容
      total: '' // 存取購物車總價
    }
  },
  created () {
    this.getProducts()
    this.getCart()
    this.clickToClose()
    // 監控購物車數量
    this.emitter.on('cart-length-change', () => {
      this.getCart()
    })
  },
  mounted () {
    // 建立下拉選單實體
    var dropdownElementList = [].slice.call(document.querySelectorAll('.dropdown-toggle'))
    this.dropdownList = dropdownElementList.map(function (dropdownToggleEl) {
      return new Dropdown(dropdownToggleEl)
    })
    this.smallPadControll()
  },
  methods: {
    // 取得購物車內容
    getCart () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      // this.isLoading = true
      this.$http.get(url)
        .then((res) => {
          this.total = res.data.data.total
          this.carts = res.data.data.carts
          this.cart_count = res.data.data.carts.length
          // this.isLoading = false
        })
    },
    // 取得全部已啟用的產品
    getProducts () {
      // this.isLoading = true
      // const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products?page=${page}`
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      // axios的$http方法
      this.$http.get(api)
        .then((res) => {
          // this.isLoading = false
          if (res.data.success) {
            // console.log(res.data.products)
            // if (this.category === res.data.products.category) {
            this.products = res.data.products
            this.pagination = res.data.pagination
            // }
          }
        })
    },
    // 點擊別處能關閉搜尋結果的ul
    clickToClose () {
      const body = document.querySelector('body')
      body.addEventListener('click', function (event) {
        if (event.target.className !== 'dropdown-item') {
          const search = document.querySelectorAll('.search-ul')
          search.forEach((item) => {
            item.style.display = 'none'
          })
        }
      })
    },
    // 點擊搜尋結果跳轉到產品詳細頁面
    pushToSingle (id) {
      this.$router.push(`/user/usersingleproduct/${id}`)
      // 使用mitt傳遞id給singleProduct頁面來取得產品內容
      this.emitter.emit('get-new-product', id)
    },
    // 點擊購物車後表格內容會用動畫方式捲到最下面
    goToBottom () {
      $('.ptn-cart-summary').animate({ scrollTop: $(document).height()-$(window).height() }, 1000)
    },
    // 點擊continue shopping 和 Edit Cart關閉 Cart table
    closeDropdownList () {
      var dropdownToggle = document.querySelector('.dropdown-toggle')
      var dropdownMenu = document.querySelector('.dropdown-menu')
      dropdownToggle.classList.remove('show')
      dropdownMenu.classList.remove('show')
    },
    // smallpad 按鈕開關功能
    smallPadControll () {
      const smallpad_btn = document.querySelector('.smallpad-btn')
      const smallpad_btn_area = document.querySelector('.smallpad-btn-area')
      const catogory_navbar_text = document.querySelectorAll('.catogory-navbar-text')
      smallpad_btn.addEventListener('click', function () {
        smallpad_btn_area.classList.toggle('show')
      })
      catogory_navbar_text.forEach((item => {
        item.addEventListener('click', function () {
        smallpad_btn_area.classList.toggle('show')
        })
      }))
    },
    // 取得分類
    getCatrgory (e) {
      this.category = e.target.innerText
      this.$router.push(`/user/${this.category}`)
      //  讓分類頁面接收分類資料且重新整理
      this.emitter.emit('get-product', this.category)
    }
  },
  inject: ['emitter'],
  computed: {
    searchArray () {
      // return this.products
      return this.products.filter((item) => item.title.match(this.search))
    }
  },
  watch: {
    search () {
      // input 有輸入就會打開列表，反之如果是空的就直接關閉列表
      if (this.search !== '') {
        const search = document.querySelectorAll('.search-ul')
        search.forEach((item) => {
          item.style.display = 'block'
        })
      } else {
        const search = document.querySelectorAll('.search-ul')
        search.forEach((item) => {
          item.style.display = 'none'
        })
      }
    }
  }
}
</script>

